<template>
  <div class="page-main">
    <div class="screen">
      <div class="bcgd"></div>
    </div>
    <div class="shading">
      <div class="blog-title"></div>
      <div class="blog-tip">
        <p class="tip-time">{{ mainObj.updated_at }}</p>
        <p class="tip-title" @click="mainRouteBtn">{{ mainObj.title }}</p>
        <p class="tip-content">{{ mainObj.body }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import config from '@/api/config/index'
export default {
  name: 'PageMain',
  components: {},
  props: {
    mainObj: {
      type: Object,
      default () {
        return {
          updated_at: '07月31日, 1992年',
          title: '我出生了',
          body: '我落地了，哈哈哈。'
        }
      }
    }
  },
  data () {
    return {}
  },
  methods: {
    GithubBtn () {
      window.open(`https://github.com/${config.user}/${config.githubBlog}`, '_blank')
    },
    mainRouteBtn () {
      this.$router.push({
        name: 'Content',
        query: {
          now: this.mainObj.number
        }
      })
    }
  },
  created () {},
  mounted () {}
}
</script>

<style lang="less">
  .page-main {
    position: relative;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    .screen {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      .bcgd {
        position: absolute;
        left: -25px;
        top: -25px;
        width: calc(~'100% + 50px');
        height: calc(~'100% + 50px');
        background: url('../assets/bcgd/home_bc.jpg') center center no-repeat;
        background-size: auto 135%;
        background-color: #eaeaea;
        animation: homebc 15s linear 0s infinite alternate;
      }
    }
    .shading {
      position: absolute;
      top: 0;
      left: 0;
      width: 60%;
      min-width: 900px;
      height: 100%;
      padding: 76px 0 0 64px;
      box-sizing: border-box;
      background: url('../assets/bcgd/shading.png') top left no-repeat;
      background-size: 100% 100%;
      .blog-title {
        height: 26px;
        background: url('../assets/blog_tl.png') top left no-repeat;
        background-size: auto 100%;
      }
      .blog-tip {
        position: absolute;
        bottom: 32%;
        left: 15%;
        width: 40%;
        color: #fff;
        box-sizing: border-box;
        .tip-time {
          font-size: 12px;
          line-height: 32px;
          color: #bbb;
        }
        .tip-title {
          display: inline-block;
          cursor: pointer;
          max-width: 90%;
          height: 64px;
          font-size: 28px;
          line-height: 64px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .tip-content {
          margin-top: 12px;
          font-size: 14px;
          line-height: 24px;
          height: 72px;
          color: #ddd;
          overflow: hidden;
        }
      }
    }
  }
  @media screen and (max-width: 600px) {
    .page-main .shading {
      top: auto;
      bottom: 0;
      padding-top: 52px;
      width: 150%;
      min-width: 548px;
      height: 65%;
      padding-left: 24px;
      .blog-tip {
        left: 24px;
        bottom: 96px;
        width: 50%;
      }
    }
  }
  @keyframes homebc {
    0% {
      top: -25px;
      left: -25px;
    }
    25% {
      top: -50px;
      left: -50px;
    }
    50% {
      top: -25px;
      left: -25px;
    }
    75% {
      top: -50px;
      left: -50px;
    }
    100% {
      top: -25px;
      left: -25px;
    }
  }
</style>
