<template>
  <div class="content-page">
    <blog-header></blog-header>
    <article-lor-c>
      <h1 class="article-title">{{ article.title }}</h1>
      <div class="article-time">{{ article.updated_at }}</div>
      <div ref="content" class="ar-content"></div>
      <template slot="footer">
        <div class="aside-article up">上一篇：
          <span :class="{'aside-title': !!upArticle}" @click="otherArticleBtn(upArticle ? upArticle.number : -1)">{{ upArticle ? upArticle.title : 'null' }}</span>
        </div>
        <div class="aside-article next">下一篇：
          <span :class="{'aside-title': !!nextArticle}" @click="otherArticleBtn(nextArticle ? nextArticle.number : -1)">{{ nextArticle ? nextArticle.title : 'null' }}</span>
        </div>
      </template>
    </article-lor-c>
  </div>
</template>

<script>
import moment from 'moment'
import BlogHeader from '@/components/common/BlogHeader'
import ArticleLorC from '@/components/article/ArticleLorC'
import {
  SingleIssue
} from '@/api/issue/index'
export default {
  name: 'Content',
  components: {
    BlogHeader,
    ArticleLorC
  },
  data () {
    return {
      issueNumber: '',
      article: {},
      upArticle: null,
      nextArticle: null
    }
  },
  watch: {
    issueNumber (val) {
      if (val <= 0) return
      this.initContent()
      this.initFooter()
    }
  },
  methods: {
    async initContent () {
      let data = await SingleIssue({
        number: this.issueNumber
      })
      data.updated_at = moment(data.updated_at).format('MM月DD日，YYYY年')
      this.article = data
      this.$refs['content'].innerHTML = this.$marked(data.body)
    },
    initFooter () {
      // up
      SingleIssue({
        number: this.issueNumber - 1
      }).then(data => {
        this.upArticle = data
      }).catch(() => {
        this.upArticle = null
      })
      // next
      SingleIssue({
        number: this.issueNumber + 1
      }).then(data => {
        this.nextArticle = data
      }).catch(() => {
        this.nextArticle = null
      })
    },
    otherArticleBtn (number) {
      if (number === -1) return
      this.$router.push({
        name: 'Content',
        query: {
          now: number
        }
      })
      this.issueNumber = Number.parseInt(number)
    }
  },
  created () {
    this.issueNumber = this.$route.query && Number.parseInt(this.$route.query.now)
  }
}
</script>

<style lang="less">
  .content-page {
    height: 100%;
    .article-time {
      padding-right: 8px;
      text-align: right;
      color: #666;
    }
    .ar-content {
      overflow: hidden;
      img {
        max-width: 100%;
      }
    }
    .aside-article {
      max-width: 45%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      .aside-title {
        cursor: pointer;
        color: #7b2cff;
        &:hover {
          color: #7b2cca;
        }
      }
      &.up {
        float: left;
      }
      &.next {
        float: right;
      }
    }
  }
</style>
