<template>
  <div class="article-page">
    <blog-header></blog-header>
    <article-lor-c>
      <div v-if="logList.length === 0" class="no-content">暂无数据</div>
      <ul v-else class="article-list">
        <li class="article-item item-header">
          <span class="i-title">标题</span>
          <span class="i-content">内容</span>
          <span class="i-time">更新时间</span>
        </li>
        <li class="article-item item" v-for="item in logList" :key="item.id">
          <span class="i-title" @click="objContentBtn(item)">{{ item.title }}</span>
          <span class="i-content">{{ item.body }}</span>
          <span class="i-time">{{ item.updated_at }}</span>
        </li>
      </ul>
      <template slot="footer">
        <list-footer class="page-footer" :has-more="hasMore" :more-mood-btn="(e) => { moreMoodBtn() }"></list-footer>
      </template>
    </article-lor-c>
  </div>
</template>

<script>
import moment from 'moment'
import BlogHeader from '@/components/common/BlogHeader'
import ArticleLorC from '@/components/article/ArticleLorC'
import ListFooter from '@/components/common/ListFooter'
import {
  IssuePage
} from '@/api/issue/index'
export default {
  name: 'Article',
  components: {
    BlogHeader,
    ArticleLorC,
    ListFooter
  },
  data () {
    return {
      labels: '',
      logList: [],
      pageInfo: {
        page: 1,
        per_page: 10
      },
      hasMore: false
    }
  },
  methods: {
    async initIssuePage () {
      let data = await IssuePage(Object.assign({
        labels: this.labels
      }, this.pageInfo))
      data.forEach(ele => {
        ele.updated_at = moment(ele.updated_at).format('YYYY年MM月DD日')
      });
      this.pageInfo.page === 1 && (this.logList = [])
      this.logList = this.logList.concat(data)
      if (data.length < this.pageInfo.per_page) this.hasMore = false
      else {
        let pageCheckData = await IssuePage(Object.assign({
          labels: this.labels
        }, this.pageInfo, {
          page: this.pageInfo.page + 1
        }))
        this.hasMore = pageCheckData.length === 0 ? false : true
      }
    },
    objContentBtn (obj) {
      // let index = this.logList.findIndex(item => item.number === obj.number)
      this.$router.push({
        name: 'Content',
        query: {
          now: obj.number
        }
      })
    },
    async moreMoodBtn () {
      if (!this.hasMore) return
      this.pageInfo.page ++
      this.initIssuePage()
    }
  },
  created () {
    this.labels = this.$route.query && this.$route.query.label
    this.initIssuePage()
  },
  mounted () {}
}
</script>

<style lang="less">
  .article-page {
    height: 100%;
    .no-content {
      height: 200px;
      line-height: 198px;
      font-size: 14px;
      text-align: center;
      color: #aaeaaa;
      border-bottom: 1px solid #eaeaea;
    }
    .article-list {
      margin: 0;
      padding: 0 16px;
      list-style: none;
      .article-item {
        height: 36px;
        line-height: 24px;
        margin: 0 0 8px;
        padding-top: 8px;
        box-sizing: border-box;
        border-top: 1px solid #eaeaea;
        > span {
          display: inline-block;
          padding: 0 16px;
          font-size: 14px;
          text-align: center;
          vertical-align: middle;
          box-sizing: border-box;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .i-title {
          cursor: pointer;
          width: 20%;
          font-size: 15px;
          font-weight: 500;
          border-right: 1px solid #cacaca;
          &:hover {
            color: #555;
          }
        }
        .i-content {
          width: 60%;
          border-right: 1px solid #cacaca;
        }
        .i-time {
          font-size: 12px;
          width: 20%;
        }
        &.item:hover {
          background-color: #ccc;
        }
        &.item-header {
          > span {
            font-weight: bold;
          }
        }
        &:first-child {
          border-top: none;
          padding-top: 0;
          height: 24px;
        }
      }
    }
    .page-footer {
      font-size: 12px;
      text-align: center;
      box-sizing: border-box;
      background-color: #fff;
    }
  }
</style>

