<template>
  <div class="home-page">
    <span class="home-setting iconfont icon-caidan-tianchong" title="导航" @click="showMenu"></span>
    <blog-menu ref="menu"></blog-menu>
    <page-main :copy-right="true" :main-obj="mainObj"></page-main>
    <copy-right></copy-right>
  </div>
</template>

<script>
import moment from 'moment'
// @ is an alias to /src
import PageMain from '@/components/PageMain'
import BlogMenu from '@/components/common/BlogMenu'
import CopyRight from '@/components/common/CopyRight'
import {
  IssuePage
} from '@/api/issue/index'

export default {
  name: 'Home',
  components: {
    PageMain,
    BlogMenu,
    CopyRight
  },
  data () {
    return {
      mainObj: {}
    }
  },
  methods: {
    showMenu () {
      this.$refs['menu'].show()
    },
    async initMainIssue () {
      let list = await IssuePage({
        labels: '心情日志'
      })
      if (list && list.length > 0) {
        let obj = list[0]
        obj.updated_at = moment(obj.updated_at).format('MM月DD日，YYYY年')
        obj.body = obj.body.length > 65 ? `${obj.body.substring(0, 65)}...` : obj.body
        this.mainObj = list[0]
      }
    }
  },
  created () {
    this.initMainIssue()
  },
  mounted () {}
}
</script>

<style lang="less">
  .home-page {
    height: 100%;
    .home-setting {
      cursor: pointer;
      position: fixed;
      right: 64px;
      top: 48px;
      width: 28px;
      height: 28px;
      line-height: 28px;
      font-size: 20px;
      text-align: center;
      border: 1px solid transparent;
      border-radius: 4px;
      color: #444;
      background-color: #fff;
      z-index: 99;
      &:hover {
        color: #333;
        background-color: #eaeaea;
      }
    }
  }
  @media screen and(max-width: 600px) {
    .home-page .home-setting {
      right: 48px;
    }
  }
</style>

