import config from '../config/index'
import http from '../config/http'

let url = `repos/${config.user}/${config.githubBlog}/issues`

let IssuePage = (ps) => {
  let params = Object.assign({
    filter: 'created', // assigned created mentioned subscribed all
    state: 'all', // open close all
    labels: 'all',
    sort: 'updated', // created updated comments
    page: 1,
    per_page: 10
  }, ps)
  return http.get(url, {
    params
  })
}

let SingleIssue = (rest) => {
  return http.get(`${url}/${rest.number}`)
}

export {
  IssuePage,
  SingleIssue
}
