<template>
  <div class="mood-page">
    <blog-header></blog-header>
    <mood-list :single-list="true">
      <mood-item v-for="(item, index) in logList" :key="item.id" :image-right="index % 2 === 1" :obj="item" :obj-content-btn="(e) => { objContentBtn(e, item) }"></mood-item>
      <template slot="footer">
        <list-footer :has-more="hasMore" :more-mood-btn="(e) => { moreMoodBtn() }"></list-footer>
      </template>
    </mood-list>
  </div>
</template>

<script>
import moment from 'moment'
import BlogHeader from '@/components/common/BlogHeader'
import MoodList from '@/components/mood/MoodList'
import MoodItem from '@/components/mood/MoodItem'
import ListFooter from '@/components/common/ListFooter'
import defaultUrl from '@/assets/default/default_item_1.jpg'
import {
  IssuePage
} from '@/api/issue/index'
export default {
  name: 'Mood',
  components: {
    BlogHeader,
    MoodList,
    MoodItem,
    ListFooter
  },
  props: {},
  data () {
    return {
      logList: [],
      pageInfo: {
        page: 1,
        per_page: 10
      },
      hasMore: false
    }
  },
  methods: {
    async initIssuePage () {
      let data = await IssuePage(Object.assign({
        labels: '心情日志'
      }, this.pageInfo))
      data.forEach(ele => {
        ele.updated_at = moment(ele.updated_at).format('MM月DD日，YYYY年')
        // 限定心情日志 上传图片名称mood-image
        let rex = ele.body.match(/!\[mood-image\]\((.+)\)/)
        ele.imageUrl = rex ? rex[1] : defaultUrl
        ele.body = ele.body.length > 65 ? `${ele.body.substring(0, 65)}...` : ele.body
      });
      this.pageInfo.page === 1 && (this.logList = [])
      this.logList = this.logList.concat(data)
      if (data.length < this.pageInfo.per_page) this.hasMore = false
      else {
        let pageCheckData = await IssuePage(Object.assign({
          labels: '心情日志'
        }, this.pageInfo, {
          page: this.pageInfo.page + 1
        }))
        this.hasMore = pageCheckData.length === 0 ? false : true
      }
    },
    objContentBtn (e, obj) {
      // let index = this.logList.findIndex(item => item.number === obj.number)
      this.$router.push({
        name: 'Content',
        query: {
          now: obj.number
        }
      })
    },
    async moreMoodBtn () {
      if (!this.hasMore) return
      this.pageInfo.page ++
      this.initIssuePage()
    }
  },
  created () {
    this.initIssuePage()
  },
  mounted () {}
}
</script>

<style lang="less">
  .mood-page {
    height: 100%;
  }
</style>
