<template>
  <div class="blog-menu" :class="{active: active}">
    <span class="blog-close iconfont icon-guanbi" @click="close"></span>
    <span class="blog-title"></span>
    <div class="menu-top"></div>
    <div class="menu-content">
      <ul class="labels default-labels">
        <li v-for="item in defaultLabels" :key="item.id" class="label-item" @click="(e) => { routeBtn(e, item.name.replace(' ', '_'), true) }">{{ item.name }}</li>
      </ul>
      <div class="labels owner-labels">
        <ul class="label-tabs">
          <!-- :class="{'label-active': activeTab === item.name}" -->
          <li v-for="item in userLabels" :key="item.id" class="label-item" @click="(e) => { routeBtn(e, item.name) }">{{ item.name }}</li>
        </ul>
      </div>
    </div>
    <copy-right></copy-right>
  </div>
</template>

<script>
import CopyRight from '@/components/common/CopyRight'
import {
  LabelAll
} from '@/api/label/index'
export default {
  name: 'BlogMenu',
  components: {
    CopyRight
  },
  props: {},
  data () {
    return {
      active: false,
      defaultLabels: [],
      userLabels: []
      // activeTab: ''
    }
  },
  methods: {
    show () {
      this.active = true
    },
    close () {
      this.active = false
    },
    routeBtn (e, name, def) {
      e.stopPropagation()
      let sn = name
      if (def) sn = sn.replace('_', ' ')
      sn.indexOf('心情日志') === -1
        ? this.$router.push({
          name: 'Article',
          query: {
            label: sn
          }
        })
        : this.$router.push({ name: 'Mood' })
      this.close()
    },
    async initLabels () {
      let data = await LabelAll()
      data.forEach(ele => {
        if (ele.name.split(':').length > 1) return
        ele.default
          ? this.defaultLabels.push(ele)
          : this.userLabels.push(ele)
      })
    }
  },
  created () {
    this.initLabels()
  },
  mounted () {}
}
</script>

<style lang="less">
  .blog-menu {
    position: fixed;
    top: -100%;
    bottom: 100%;
    left: 0;
    right: 0;
    background-color: #fafafa;
    transition: all 0.4s linear;
    z-index: 99999;
    &.active {
      top: 0;
      bottom: 0;
    }
    .blog-title {
      cursor: pointer;
      position: absolute;
      left: 48px;
      top: 154px;
      width: 126px;
      height: 22px;
      background: url('../../assets/blog_tl.png') top left no-repeat;
      background-size: auto 100%;
    }
    .blog-close {
      cursor: pointer;
      position: absolute;
      right: 48px;
      top: 148px;
      width: 26px;
      height: 26px;
      font-weight: bold;
      line-height: 26px;
      text-align: center;
      border: 1px solid transparent;
      border-radius: 100%;
      color: #fff;
      background-color: #ccc;
      &:hover {
        background-color: #aaa;
      }
    }
    .menu-top {
      height: 192px;
      background: url('../../assets/bcgd/menu_bc.jpg') center center no-repeat;
      background-size: 100% auto;
    }
    .menu-content {
      height: calc(~'100% - 286px');
      margin: 16px 32px 0;
      padding: 16px 0;
      border-top: 1px solid #eaeaea;
      border-bottom: 1px solid #eaeaea;
      box-sizing: border-box;
      overflow: hidden;
      .labels {
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
      }
      .default-labels {
        margin: 0;
        float: left;
        width: 216px;
        border-right: 1px solid #eaeaea;
        list-style: none;
        .label-item {
          cursor: pointer;
          margin-right: 16px;
          height: 42px;
          line-height: 42px;
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          border-bottom: 1px solid #eaeaea;
          color: #666;
          &:hover {
            background-color: #eaeaea;
          }
          &:last-child {
            border: none;
          }
        }
      }
      .owner-labels {
        margin-left: 232px;
        .label-tabs {
          margin: 0;
          list-style: none;
          .label-item {
            cursor: pointer;
            display: inline-block;
            width: 156px;
            height: 32px;
            margin: 0 16px;
            border-bottom: 1px solid #eaeaea;
            line-height: 32px;
            text-align: center;
            &:hover {
              background-color: #cacaca;
              border-color: #acea00;
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 600px) {
    .blog-menu {
      .menu-top {
        background-size: auto 160%;
      }
      .menu-content {
        padding: 8px 0;
        .default-labels {
          width: 136px;
          .label-item {
            margin-right: 8px;
            height: 32px;
            line-height: 32px;
            font-size: 12px;
          }
        }
        .owner-labels {
          margin-left: 144px;
        }
      }
    }
  }
</style>

